/* Animação de balançar */
@keyframes bounce {
  0%,
  20%,
  60%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  40% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }

  80% {
    -webkit-transform: translateY(-5px);
    transform: translateY(-5px);
  }
}

nav {
  position: fixed;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  


  width: 10%;
  height: 100%;

  background-color: #181818;
}

/* Cabeçalho da Navbar */

#nav_header {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  height: 30%;

  background-color: black;
}

#nav_header Link {
  text-decoration: none;
  cursor: pointer;

  color: #f1f1f1;
}

#nav_header img {
  cursor: pointer;

  margin-left: 5%;

  max-width: 150px;
  width: 60%;
  padding: 15%;

  transition: all 1s ease;
}

#nav_header img:hover {
  animation: bounce 1s;
}

#titulo_span {
  cursor: pointer;

  font-size: 1.5rem;
  font-weight: 700;

  margin-bottom: 10px;
}

#sub_span {
  align-self: center;
  cursor: pointer;

  font-size: 0.8rem;
  font-weight: 300;
}

/* Sessão central para acesso as abas */

#sessoes {
  display: flex;
  justify-content: center;

  width: 100%;
  max-width: 400px;
  max-height: 50%;

  font-size: 1rem;
  margin-right: 60%;
}

#sessoes li {
  list-style: none;

  width: 100%;

  padding: 25%;

  border-top: 1px solid #1d1d1d;
  border-bottom: 1px solid #1d1d1d;
}

.componente_lista {
  text-decoration: none;
  cursor: pointer;

  transition: all 1s ease;
  color: gray;
}

.componente_lista:hover {
  color: #fd9f02;
  text-shadow: 0px 0px 10px #fd9f02cc, 0px 0px 2px #fd9f02;
}

/* Sessão relativa as redes sociais */

#redes_sociais {
  width: 100%;
  height: 35%;
}

#redes_sociais ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  list-style: none;

  padding: 5%;
}

#redes_sociais img {
  width: 95%;
}

#redes_sociais li:hover {
  animation: bounce 1.2s;
}

/* Menu responsivo */

#menu_mobile{
  display: none;

  position: fixed;
  justify-content: center;
  align-items: center;

  z-index: 1;
}

.menu_button{
  position: fixed;
  margin-left: auto;
  cursor: pointer;
  background-color: #181818;
  border: none;

  right: 95.5%;
  bottom: 95.3%;

  z-index: 1;
}

.menu.active{
  opacity: 1;
  visibility: visible;

  width: 5%;

  top: 0%;
  right: 85%;

  padding-right: 10%;


  z-index: 0;

  
}

.menu.inactive{
  display: none;
}

.menu.active ul{
  display: flex;
  flex-direction: column;
  list-style: none;
}

.menu.active li{
  position: relative;
  
  top: 5%;
  right: 30%;

  padding: 30%;

  width: 100%;
  
  border-bottom: 2px solid #1d1d1d;
}

#menu_mobile .componente_lista{
  padding: 15%;
  padding-right: 40%;
}

.menu {
  opacity: 0;
}

.mobile_sociais{
  position: relative;
  justify-content: space-evenly;
  display: grid;
  grid-template-columns:  1fr 1fr 1fr;

  width: 280%;
  bottom: 45%;
  left: 100%;
}
